import React, { useState, useContext, useEffect} from 'react';
import ContactContext from '../../context/contact/contactContext';

const ContactsForm = () => {
    const contactContext = useContext(ContactContext);

    const {addContact, updateContact, clearCurrent, current} = contactContext;

    useEffect(() => {
        if(current !== null) {
            setContact(current);
        } else {
            setContact({
                name: '',
                email: '',
                phone: '',
                type: 'personal' 
            });   
        }
    }, [contactContext, current]
    );

    const [contact, setContact] = useState({
        name: '',
        email: '',
        phone: '',
        type: 'personal'
    });

    const {name, email, phone, type} = contact;

    const onChange = e => setContact({...contact, [e.target.name]: e.target.value});

    const onSubmit = e => {
        e.preventDefault();
        if (current === null) {
            addContact(contact);
        } else {
            updateContact(contact);
        }
        
        setContact({
            name: '',
            email: '',
            phone: '',
            type: 'personal' 
        })
    };

    const clearAll = () => {
        clearCurrent();
    }

    return (
        <form onSubmit={onSubmit}>
            <h2 className='text-success'>{current ? 'Edit Contact' : 'Add Contact'}</h2>
            <input
                type='text'
                placeholder='Name'
                name='name'
                value={name}
                onChange={onChange}
            />
            <input
                type='email'
                placeholder='Email'
                name='email'
                value={email}
                onChange={onChange}
            />
            <input
                type='text'
                placeholder='Phone'
                name='phone'
                value={phone}
                onChange={onChange}
            />
            <h5>Contact Type</h5>
            <input
                type='radio'
                value='personal'
                name='type'
                checked={type === 'personal'}
                onChange={onChange}
            /> Personal {' '}
            <input
                type='radio'
                value='professional'
                name='type'
                checked={type === 'professional'}
                onChange={onChange}
            /> Professional

            <div>
                <input
                    type='submit'
                    value={current ? 'Update Contact' : 'Add Contact'}
                    className='btn btn-success btn-block'
                />
            </div>
            {current && (
                <div>
                    <button
                        className='btn btn-light btn-block'
                        onClick={clearAll}
                    >Clear</button>
                </div>
            )}
        </form>
    )
}

export default ContactsForm